// Tables

.table > thead > tr {
  border-top: 1px solid $border-color;
  background-color: #f9f9fa; }

.table > tbody > tr:last-child, .table > tfoot > tr {
  border-bottom: 1px solid $border-color; }

.table-bordered {
  border: 0; }

.table > thead > tr > th, .table > tbody > tr > th, .table > tfoot > tr > th, .table > thead > tr > td, .table > tbody > tr > td, .table > tfoot > tr > td {
  padding: 11px;
  line-height: 1.25;
  font-size: 14px;
  color: $color-dark-gray;
  vertical-align: middle; }

.table > thead > tr > th {
  padding: 15px;
  border-bottom: none;
  vertical-align: bottom;
  font-size: 11px;
  text-transform: uppercase;
  color: $color-dark-gray;
  font-weight: bold;
  letter-spacing: 1px; }

.table-bordered > thead > tr > th:first-child, .table-bordered > tbody > tr > th:first-child, .table-bordered > tfoot > tr > th:first-child, .table-bordered > thead > tr > td:first-child, .table-bordered > tbody > tr > td:first-child, .table-bordered > tfoot > tr > td:first-child {
  border-left: none; }

.table-bordered > thead > tr > th:last-child, .table-bordered > tbody > tr > th:last-child, .table-bordered > tfoot > tr > th:last-child, .table-bordered > thead > tr > td:last-child, .table-bordered > tbody > tr > td:last-child, .table-bordered > tfoot > tr > td:last-child {
  border-right: none; }

@media screen and (max-width: 767px) {
  .table-responsive {
    border-top: none;
    border-bottom: none; } }

.table-striped > tbody > tr:nth-of-type(odd) {
  background-color: transparent; }

.table-striped > tbody > tr:nth-of-type(even) {
  background-color: $color-bright; }

.table-hover > tbody > tr {
  transition: background-color .1s ease-out; }

.table-hover > tbody > tr:hover {
  background-color: #f2f2f5; }

.table-hover > tbody > tr:hover td {
  color: $text-color; }

table.dataTable {
  margin-bottom: 18px !important;
  border-collapse: collapse !important; }

table.dataTable thead .sorting:after, table.dataTable thead .sorting_asc:after, table.dataTable thead .sorting_desc:after, table.dataTable thead .sorting_asc_disabled:after, table.dataTable thead .sorting_desc_disabled:after {
  content: '' !important;
  background-image: url(/images/sorting-icon.svg);
  height: 10px;
  width: 12px;
  bottom: auto;
  top: 50%;
  -ms-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  right: 8px;
  display: block;
  font-family: inherit;
  opacity: 0.4; }

table.dataTable thead .sorting_desc:after, table.dataTable thead .sorting_asc:after {
  opacity: 1; }

.dataTables_length, .dataTables_info {
  color: #7c8998;
  .form-control {
    width: 50px;
    margin-left: 5px; } }

.datatable-action-padded {
  padding-right: 40px !important;
  position: relative; }

.datatable-action {
  position: absolute;
  right: 3px;
  top: 50%;
  z-index: 2;
  -ms-transform: translateY(-50%);
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%); }

.datatable-action.open {
  z-index: 4; }

.table-hover > tbody > tr:hover .btn-circle.btn-datatable-action {
  opacity: 1; }

