.header-line.extreme-color  {
  width: 20%;
}

.header-line {
  height: 6px;
  width: 10%;
  float: left;
  margin: 0;
  position: relative;
}

.header-line div {
  position: absolute;
  top: 70px;
  left:15px;
  font-weight: 600;
  color: #fff;
}

.header-line div span {
  font-weight: 400;
  display: block;
  font-size: 12px;
  @include color-transparent(#ffffff,0.6);
}

.color-demo .header-line
{
  height: 118px;
}


.color-demo .header-line:after {
  display: block;
  position: absolute;
  top: 0;
  @include bg-transparent(#000000,0.2);
  content: '';
  height: 10px;
  width: 100%
}

/* Buttons */

.btn {
  padding: 7px 20px;
  border-radius: 4px;
}

.float-e-margins .btn {
  margin-bottom: 5px;
}

.btn-lg, .btn-group-lg > .btn {
  font-size: 16px;
  padding: 12px 20px;
  font-weight: 400;
  border-radius: 4px;
}

.btn-sm, .btn-group-sm > .btn {
  border-radius: 4px;
  font-size: 13px;
  line-height: 16px;
  padding: 6px 20px;
}

.btn-xs, .btn-group-xs > .btn {
  border-radius: 4px;
  font-size: 11px;
  line-height: 14px;
  padding: 2px 20px;
}

