.btn-circle {
  width: 30px;
  height: 30px;
  padding: 6px 0;
  border-radius: 15px;
  text-align: center;
  font-size: 12px;
  line-height: 1.428571429;
}

.btn-circle.btn-xs {
  width: 20px;
  height: 20px;
  padding: 4px 8px;
  border-radius: 25px;
  font-size: 10px;
  line-height: 1;
}

.btn-circle.btn-datatable-action {
  margin-right: 7px;
  font-size: 20px;
  line-height: 1;
  padding: 0;
  background-color: transparent;
  color: #7c8998;
  border-color: #7c8998;
  opacity: 0;
  transition: all .1s ease-out;

  i {
    left: -1px;
    top: -1px;
    position: relative;
  }
}

.btn-circle.btn-lg {
  width: 50px;
  height: 50px;
  padding: 10px 16px;
  border-radius: 25px;
  font-size: 18px;
  line-height: 1.33;
}

.btn-circle.btn-xl {
  width: 70px;
  height: 70px;
  padding: 10px 16px;
  border-radius: 35px;
  font-size: 24px;
  line-height: 1.33;
}